.TopNavbarComponent {
  padding: 1rem;
  display: flex;
  align-items: center;
  position: sticky;
  top: 0;
  background-color: var(--bgColor);
  z-index: 200;
}

.LogoDiv {
  margin-right: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.LogoDiv > div {
  margin-left: 0.5rem;
}

.LogoDiv h3 {
  font-size: 0.9rem;
}

.LogoDiv p {
  font-size: 0.8rem;
  width: max-content;
}

.NavLinkDiv {
  display: flex;
  justify-content: center;
  align-items: center;
}

.UserProfileDiv {
  position: relative;
}

.userDiv {
  width: 35px;
  height: 35px;
  background-position: center;
  background-size: cover;
  background-image: url(https://images.unsplash.com/placeholder-avatars/extra-large.jpg?dpr=1&auto=format&fit=crop&w=32&h=32&q=60&crop=faces&bg=fff);
  border-radius: 50%;
  margin-left: 1rem;
  cursor: pointer;
}

.TopNavbarContent p {
  margin: 0 1.5rem;
  color: var(--loveGray);
}

.TopNavbarContent i {
  cursor: pointer;
  color: var(--loveGray);
}

.TopNavbarContent img {
  margin: 0 2rem;
  cursor: pointer;
}

.NotifecationDiv {
  position: relative;
}
